@import url(https://fonts.googleapis.com/css2?family=Nunito:ital,wght@0,200;0,300;0,400;0,600;1,300&display=swap);
body {
  margin: 0;
  font-family: "Nunito", sans-serif;
  font-size: 1rem;
}

.Layout {
  width: 100%;
  min-height: 100vh;
  background-color: #282c34;
  color: #eee;
  padding-bottom: 1rem;
}

.Header {
  height: 4rem;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  justify-content: flex-end;
  font-size: 1.3rem;
  background-color: #282c34;
  padding-right: 0.5rem;
  box-sizing: border-box;
}
.Header nav {
  display: flex;
  height: 100%;
  width: auto;
  align-items: center;
}

.Header div,
.Header a {
  height: 100%;
  width: auto;
  padding: 1rem;
  box-sizing: border-box;
  color: #eee;
  font-weight: 300;
  transition: all 0.2s ease-in-out;
}
.Header a {
  text-decoration: none;
}

@media (min-width: 700px) {
  .Header div:hover,
  .Header a:hover {
    color: #006730;
    background-color: #ddd;

    cursor: pointer;
  }
}

.Main {
  margin-top: 4rem;
  width: 90%;
  margin: 4rem auto 0 auto;
}

@media (min-width: 1100px) {
  .Main {
    width: 70%;
  }
}

@media (min-width: 1500px) {
  .Main {
    width: 60%;
  }
}

.Table-Container {
  overflow-x: auto;
}

.Skill-Table {
  margin: auto;
  text-align: center;
}

.Skill-Table th {
  padding: 0.5rem;
  transition: all 0.3s linear;
}
.Skill-Table td {
  padding: 0.1rem 0.5rem;
  transition: all 0.5s ease-in-out;
}
.Skill-Table td span {
  color: #99ffc9;
  font-size: 1.2rem;
}

.Skill-Table thead {
  background-color: #1f6bcd;
}
.Skill-Table th:hover {
  background-color: #1a519b;
}

@media (min-width: 950px) {
  .Table-Container {
    overflow-x: hidden;
  }
  .Skill-Table th {
    min-width: 11rem;
  }
}

.Card {
  margin: 2rem auto;
  padding: 0.5rem 1rem;
  width: 100%;
  box-sizing: border-box;
  border-radius: 1rem;
  box-shadow: 0 8px 42px -8px rgba(255, 255, 255, 0.3);
  background-color: #282c34;
  color: #ddd;
}

.Card-Actions {
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.Card-Image img {
  height: 250px;
}
.Card-Image {
  display: flex;
  justify-content: center;
}

.Buttons-Actions {
  flex-grow: 2;
}

.Card h1,
h6,
h5 {
  margin: 0.1rem 0;
}

.Card p {
  margin: 0.5rem 0;
}

@media (min-width: 700px) {
  .Card-Main {
    display: flex;
  }

  .Card-Image {
    flex: 2 1;
    padding: 0 0.5rem;
  }

  .Card-Content {
    flex: 3 1;
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
    padding: 0 0.5rem;
  }

  .Card-Actions {
    flex: 1 1;
    flex-direction: column;
    justify-content: flex-start;
    padding: 0 0.5rem;
  }

  .Card-Actions a {
    margin-bottom: 1rem;
  }
}

/* @media (min-width: 1300px) {
  .Card {
    width: 60%;
  }
} */

.Button {
  text-decoration: none;
  display: flex;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
  font-size: 1.7rem;
  padding: 0.4rem 2.5rem;
  border-radius: 0.3rem;
  color: #eee;
  text-align: center;
  cursor: pointer;
}

.Button:focus {
  outline: none;
}

.ButtonPaper {
  background-color: #1ab37c;
  border-color: #1ab37c;
  transition: all 0.2s ease-in-out;
}

.ButtonPaper:hover {
  background-color: #1a9b64;
}

.ButtonCode {
  background-color: #1f6bcd;
  border-color: #1f6bcd;
  transition: all 0.2s ease-in-out;
}

.ButtonCode:hover {
  background-color: #1a519b;
}

.ButtonMedia {
  cursor: pointer;
  color: #eee;
  display: inline-block;
  text-decoration: none;
  height: 2.5rem;
  width: 2.5rem;
  font-size: 2.5rem;
  margin-right: 0.5rem;
  transition: all 0.2s ease-in-out;
}

.ButtonMedia:hover {
  color: #1f6bcd;
}

.Chip {
  display: inline-block;
  padding: 0.1rem 1rem;
  margin: 0.2rem 0.2rem 0.2rem 0;
  border: 1px solid #006730;
  color: #006730;
  border-radius: 0.7rem;
  background-color: #ddd;
}

.Main-Container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.Info-Container h2,
.Info-Container h3,
.Info-Container p {
  margin: 0.3rem 0;
}
.Image-Container img {
  height: 200px;
}

@media (min-width: 600px) {
  .Main-Container {
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-start;
    height: 200px;
    margin-bottom: 3rem;
  }
  .Image-Container {
    margin-right: 1rem;
  }

  .Info-Container {
    height: inherit;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  .Info-Container h2 {
    margin-top: 0;
  }
  .Media-Buttons {
    display: flex;
    align-items: center;
  }
}

